function addressesSlider() {
    var swiper = new Swiper('.addresses-slider .swiper-container', {
        slidesPerView: "auto",
        spaceBetween: 0,
        watchSlidesProgress: true,
        preventClicks :true,
        a11y: false,
        scrollbar: {
            el: '.addresses-slider .swiper-scrollbar',
            draggable: true,
        },
    })
}

addressesSlider()


let locationView = document.querySelector(".location-modal__view")
let locationModal = document.querySelector(".location-modal")
let locationModalClose = document.querySelector("._close-modal")

if(locationView){
    locationView.onclick = () => {
        locationModal.classList.toggle("_map-view")
    }
}


let showAddressesBtn = document.querySelector("._show-addresses")

if(showAddressesBtn){
    showAddressesBtn.onclick = () => {
        locationModal.classList.add("_active")
    }
    locationModalClose.onclick = () => {
        locationModal.classList.remove("_active")
    }
}
