let citySelect = document.querySelector(".city-select")

if(citySelect){
    citySelect.querySelector(".city-select__show").onclick = () => {
        console.log(true)
        citySelect.classList.toggle("_active")
    }

    document.addEventListener( 'click', (e) => {
        const withinBoundaries = e.composedPath().includes(citySelect);

        if ( ! withinBoundaries ) {
            citySelect.classList.remove("_active")
        }
    })
}


let burger = document.querySelector(".burger")
let body = document.querySelector("body")
let menuCloseBtn = document.querySelector(".menu__bg")

if (burger) {
    burger.onclick = function() {
        body.classList.add("_open")
        body.classList.add("fixed-body")
    }
}
if (menuCloseBtn) {
    menuCloseBtn.onclick = function() {
        body.classList.remove("_open")
        body.classList.remove("fixed-body")
    }
}
