let calculators = document.querySelectorAll(".calc")

calculators.forEach(calc => {
    let minusBtn = calc.querySelector("._minus")
    let plusBtn = calc.querySelector("._plus")
    let input = calc.querySelector("input")

    plusBtn.onclick = ()=>{
        if(plusBtn.closest(".catalog-card")){
            openModal(document.querySelector("#additiveModal"))
        }
        input.value = +input.value + 1
        calc.classList.add("_added")
    }
    minusBtn.onclick = ()=>{
        input.value = +input.value - 1
        if(+input.value === 0){
            calc.classList.remove("_added")
        }
    }
})


let additives = document.querySelectorAll(".additive-card")
let additivesCalcs = document.querySelectorAll(".additive-card .calc")
let additivesInputs = document.querySelectorAll(".additive-card .calc input")
let additiveModal = document.querySelector("#additiveModal")
let selectedAdditives = 0


additives.forEach(additive => {
    let calc = additive.querySelector(".calc")
    let plusBtn = additive.querySelector("._plus")
    let input = additive.querySelector("input")

    calc.onclick = ()=>{
        countSelectedAdditives()
        if(selectedAdditives>0){
            additiveModal.classList.add("_added")
        }else{
            additiveModal.classList.remove("_added")
        }
    }
})

function clearSelectedAdditives(){
    additivesInputs.forEach(input => {
        input.value = 0
    })
    additivesCalcs.forEach(additivesCalc => {
        additivesCalc.classList.remove("_added")
    })
}

function countSelectedAdditives() {
    let count = 0

    additivesInputs.forEach(input => {
        count += +input.value
    })

    selectedAdditives = count
}
